/*=========== SPECIAL =========== */
.hide-page {
	display: none !important;
}

body.vspec {
	font-size: 18px;
	line-height: 1.78;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
	color: #000;
	background-color: #fff;
}

body.vspec.fontsize-small {
	font-size: 14px;
}

body.vspec.fontsize-large {
	font-size: 30px;
}

body.vspec.spacing-medium {
	letter-spacing: 0.2em;
}

body.vspec.spacing-large {
	letter-spacing: 0.5em;
}

body.vspec.schemecolor-white {
	color: #fff;
	background-color: #000;
}

body.vspec.schemecolor-blue {
	color: #ff6c87;
	background-color: #c8e5ff;
}

body.vspec::before {
	content: none;
}

.vspec .wrapper,
.vspec .footer {
	padding-left: 70px;
	padding-right: 70px;
	padding-top: 0;
}

.inner-page .main > .max-wrap:before {
	display: none;
}

@media (max-width: 1439px) {
	.vspec .wrapper,
	.vspec .footer {
		padding-right: 0;
	}
}

@media (max-width: 480px) {
	.vspec .wrapper,
	.vspec .footer {
		padding-left: 0;
	}
	.vspec .wrapper {
		padding-top: 70px;
	}
}

.vspec .content {
	padding-top: 30px;
}

.vspec .article-press {
	padding-top: 30px;
	color: inherit;
}

.vspec .press-archive + .press-adt .article-press {
	padding-top: 0;
}

.vspec .press {
	margin-top: 0;
}

.vspec path,
.vspec polygon {
	fill: currentColor !important;
}

/* font */
.vspec .btn-default,
.vspec .btn-default-alt {
	font-family: inherit;
}

.vspec h1, .vspec h2, .vspec h3, .vspec h4, .vspec h5, .vspec h6 {
	color: inherit;
}

.vspec h1 {
	font-size: 1.67em;
	line-height: 1.6;
	letter-spacing: inherit;
	padding: 0;
	margin-bottom: 2em;
}

.vspec h2 {
	font-size: 1.33em;
	line-height: 1.55;
	padding: 0;
	margin: 0 0 0.8em;
}

.vspec h3 {
	font-size: 1.33em;
	line-height: 1.55;
	font-weight: 700;
	padding: 0;
	margin: 0 0 0.8em;
}

.vspec h4 {
	font-size: inherit;
	line-height: inherit;
	font-weight: 700;
	padding: 0;
	margin: 0 0 0.8em;
}

.vspec h5 {
	font-size: inherit;
	line-height: inherit;
	text-transform: uppercase;
	font-weight: normal;
	padding: 0;
	margin: 0 0 0.8em;
}

.vspec h6 {
	font-size: inherit;
	line-height: inherit;
	font-weight: 500;
	padding: 0;
	margin: 0 0 0.8em;
}

.vspec p {
	letter-spacing: inherit;
	margin-bottom: 0.8em;
	padding: 0;
}

.vspec .to-print {
	display: none;
}

.file__info {
	letter-spacing: normal;
}

.vspec .img-group {
	display: none;
}

.vspec.imageson .img-group {
	display: block;
}

