/* bg end */
/* slider reset */
.vspec .uncos-slider .slick-track,
.vspec .questions-slider .slick-track,
.vspec .promo-slider .slick-track {
	opacity: 1 !important;
	left: auto !important;
	width: auto !important;
	-webkit-transform: none !important;
	-ms-transform: none !important;
	transform: none !important;
}

.vspec .uncos-slider .slick-slide,
.vspec .questions-slider .slick-slide,
.vspec .promo-slider .slick-slide {
	position: relative !important;
	width: auto !important;
	opacity: 1 !important;
	left: auto !important;
	top: auto !important;
	float: none !important;
	z-index: auto !important;
}

.vspec .uncos-slider .slick-arrow,
.vspec .questions-slider .slick-arrow,
.vspec .promo-slider .slick-arrow {
	display: none !important;
}

.vspec .uncos-slider .slick-dots,
.vspec .questions-slider .slick-dots,
.vspec .promo-slider .slick-dots {
	display: none !important;
}

/* slider reset end */
@media (min-width: 980px) {
	.nav-opened .vspec .nav-overlay-page {
		display: none;
	}
}

@media (min-width: 768px) {
	.nav-opened .vspec.main-page .nav-overlay-page {
		display: none;
	}
}

@media (max-width: 479px) {
	.vspec .header .nav-overlay-page {
		height: calc(100% + 70px);
		top: -70px;
	}
}

.vspec.inner-page .header {
	position: relative;
	left: auto;
	top: auto;
}

.vspec .header-holder {
	height: auto;
	border: 2px solid currentColor;
	border-width: 0 2px 2px 0;
	padding: 0;
}

@media (max-width: 979px) {
	.vspec.inner-page .header-holder {
		min-height: 142px;
	}
}

.vspec .header-options__holder {
	border-bottom: 2px solid currentColor;
}

.vspec .logo {
	box-shadow: none;
	border: 0 solid currentColor;
	border-width: 0 2px 2px 2px;
	box-sizing: border-box;
	height: 202px;
}

.vspec.schemecolor-white .logo > a::before,
.vspec.schemecolor-white .logo > span::before {
	-webkit-filter: grayscale(1) contrast(3) invert(0);
	filter: grayscale(1) contrast(3) invert(0);
}

.vspec.schemecolor-blue .logo > a::before,
.vspec.schemecolor-blue .logo > span::before {
	-webkit-filter: grayscale(1) contrast(1) invert(1);
	filter: grayscale(1) contrast(1) invert(1);
}

.vspec.main-page .logo {
	height: 174px;
}

@media (max-width: 979px) {
	.vspec.inner-page .logo {
		height: 80px;
		border: none;
	}
}

@media (max-width: 767px) {
	.vspec .logo {
		height: 80px;
		border: none;
	}
	.vspec.main-page .logo {
		height: 80px;
	}
}

.logo-reduce .vspec .logo {
	height: 200px;
}

.vspec .logo > a:before,
.vspec .logo > span:before,
.vspec .logo > a:after,
.vspec .logo > span:after {
	background-color: transparent;
}

.vspec .logo > a:before,
.vspec .logo > span:before {
	opacity: 1;
	background-image: url(../img/logo.png);
}

.vspec.inner-page .logo > a:after,
.vspec.inner-page .logo > span:after {
	opacity: 0;
}

.vspec .logo > a:after,
.vspec .logo > span:after {
	-webkit-background-size: contain;
	background-size: contain;
	width: calc(100% - 20px);
	left: 10px;
}

@media (max-width: 767px) {
	.vspec .logo > a:before,
	.vspec .logo > span:before {
		opacity: 0;
	}
	.vspec .logo > a:after,
	.vspec .logo > span:after {
		opacity: 1;
	}
}

@media (max-width: 979px) {
	.vspec.inner-page .logo > a:before,
	.vspec.inner-page .logo > span:before {
		opacity: 0;
	}
	.vspec.inner-page .logo > a:after,
	.vspec.inner-page .logo > span:after {
		opacity: 1;
	}
}

@media (max-width: 359px) {
	.vspec .logo {
		width: 140px;
	}
}

.vspec .questions-slider {
	height: auto;
}

.vspec .questions-slider .slick-track {
	column-count: 2;
	column-gap: 2em;
}

.vspec.fontsize-small .questions-slider .slick-track {
	column-count: 3;
}

.vspec.fontsize-large .questions-slider .slick-track {
	column-count: 1;
}

@media (max-width: 979px) {
	.vspec .questions-slider .slick-track {
		column-count: 1;
		column-gap: 1.5em;
	}
	.vspec.fontsize-small .questions-slider .slick-track {
		column-count: 2;
	}
}

@media (max-width: 767px) {
	.vspec.fontsize-small .questions-slider .slick-track {
		column-count: 1;
	}
}

.vspec .questions-slider .questions__item {
	margin-bottom: 0.8em;
	display: inline-block;
	width: 100% !important;
}

.vspec .questions__item:last-child {
	margin-bottom: 0;
}

.vspec .questions__item > a {
	display: inline;
	font-size: 1.33em;
	line-height: 1.55;
	letter-spacing: inherit;
	padding: 0;
	min-height: 0;
	text-decoration: underline;
	-webkit-transition: none;
	transition: none;
}

.vspec .questions__item > a:hover {
	text-decoration: none;
}

.vspec .questions__item .questions__icon {
