}

.vspec .questions__item .questions__text {
	display: inline;
	letter-spacing: inherit;
	width: auto;
	padding: 0;
	margin-bottom: 0.8em;
	text-align: left;
}

.vspec .phones {
	box-sizing: border-box;
	box-shadow: none;
}

.vspec .phs__list > .phs__item:before {
	border-color: #000;
	display: none;
}

.vspec .phs__list > .phs__item > .phs__btn,
