	width: calc(100% + 2px);
	left: -2px;
}

@media (max-width: 479px) {
	.vspec .search-form {
		width: 100%;
		left: 0;
	}
}

.vspec .search-form input[type="search"],
.vspec .search-form input[type="text"] {
	font-size: 20px;
	line-height: 26px;
	padding: 14px 0 18px;
	height: 60px;
