	box-shadow: none;
}

.vspec .label-holder,
.vspec label {
	color: inherit;
	font-size: inherit;
	line-height: inherit;
	letter-spacing: inherit;
}

.vspec .arrow,
.vspec .slider-typography .slick-prev,
.vspec .slider-typography .slick-next {
	-webkit-filter: grayscale(1) contrast(1) invert(1);
	filter: grayscale(1) contrast(1) invert(1);
}

.vspec .arrow,
.vspec .arrow > i,
.vspec .slider-typography .slick-prev,
.vspec .slider-typography .slick-next,
.vspec .slider-typography .slick-prev:before,
.vspec .slider-typography .slick-next:before,
.vspec .slider-typography .slick-prev:after,
.vspec .slider-typography .slick-next:after {
	-webkit-transition: none;
	transition: none;
}

.vspec .img-group__item.video-preview a.img:before {
	background-color: #000;
}

.vspec .img,
.vspec .img-group__item.video-preview .img {
	display: none;
	box-shadow: 0 0 0 2px #000;
	transition: none;
	margin-bottom: 2em;
}

.vspec.imageson .img,
.vspec.imageson .img-group__item.video-preview .img {
	display: block;
}

.vspec a.img:hover,
.vspec .img-group__item.video-preview a.img:hover {
	box-shadow: 0 0 0 4px #000;
}

