.vspec .logo > a:after,
.vspec .logo > span:after {
	-webkit-background-size: contain;
	background-size: contain;
	width: calc(100% - 20px);
	left: 10px; }

@media (max-width: 767px) {
	.vspec .logo > a:before,
	.vspec .logo > span:before {
		opacity: 0; }
	.vspec .logo > a:after,
	.vspec .logo > span:after {
		opacity: 1; } }

@media (max-width: 979px) {
	.vspec.inner-page .logo > a:before,
	.vspec.inner-page .logo > span:before {
		opacity: 0; }
	.vspec.inner-page .logo > a:after,
	.vspec.inner-page .logo > span:after {
		opacity: 1; } }

@media (max-width: 359px) {
	.vspec .logo {
		width: 140px; } }

.vspec .questions-slider {
	height: auto; }

.vspec .questions-slider .slick-track {
	column-count: 2;
	column-gap: 2em; }

.vspec.fontsize-small .questions-slider .slick-track {
	column-count: 3; }

.vspec.fontsize-large .questions-slider .slick-track {
	column-count: 1; }

@media (max-width: 979px) {
	.vspec .questions-slider .slick-track {
		column-count: 1;
		column-gap: 1.5em; }

	.vspec.fontsize-small .questions-slider .slick-track {
		column-count: 2; } }

@media (max-width: 767px) {
	.vspec.fontsize-small .questions-slider .slick-track {
		column-count: 1; } }

.vspec .questions-slider .questions__item {
	margin-bottom: $bottom-space;
	display: inline-block;
	width: 100% !important; }

.vspec .questions__item:last-child {
	margin-bottom: 0; }

.vspec .questions__item > a {
