	letter-spacing: inherit;
	width: auto;
	padding: 0;
	margin-bottom: $bottom-space;
	text-align: left; }

.vspec .phones {
	box-sizing: border-box;
	box-shadow: none; }

.vspec .phs__list > .phs__item:before {
