.vspec .phs__container > .phs__list > .phs__item:hover > .phs__btn,
.vspec .phs__container > .phs__list > .phs__item.show-drop > .phs__btn,
.vspec .phones-clone:hover > .phs-clone__btn,
.vspec .phones-clone.show-drop > .phs-clone__btn {
