	color: inherit;
	border: none;
	background: transparent;
	font-family: inherit; }

.vspec .search-form .btn-search input,
.vspec .search-form .btn-search input:hover {
	background: transparent; }

.vspec .btn-search,
.vspec .btn-search::before,
.vspec .btn-search::after,
