//eight: 112px

.vspec .nav__list {
	border-color: currentColor; }

.vspec .nav__list li,
.vspec .nav__list li > a {
	border-color: currentColor;
	letter-spacing: normal; }

.vspec .nav__list li > a,
.vspec .nav__list li.hover > a,
